<template>
  <div class="container">
    <hr>
    <!-- CREATE POST HERE -->
    <div class="create-post">
      <div class="row">
        <div class="col-4">
          <label for="create-post">
           Add New Post...
          </label>
        </div>
        <div class="col-6">
          <input type="text" class="form-control" id="create-post" v-model="text" placeholder="Create Post">
        </div>
        <div class="col-2">
          <button class="btn btn-success" v-on:click="createPost">Add Post</button>
        </div>
      </div>   
    </div>
    <hr>
    <h3>Latest Posts</h3>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div class="post"
      v-for="(post, index) in posts"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      v-on:dblclick="deletePost(post._id)"
      >
      {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}/`}}
       <p class="text">{{ post.text }}</p> 
      </div>        
    </div>
    </div>
  </div>
</template>

<script>

import PostService from '../PostService';

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}
</script>

<style scoped>
.btn {
  border-radius: 0px;
}
div.container {
  max-width: 800px;
  margin: 0 auto;
}
p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}
div.post {
  position: relative;
  border: 1px solid #5bd658;
  background-color: #bcffb8;
  padding: 10px 10px 30px 10px;
  margin-bottom: 15px;
}
div.created-at {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 15px 5px 15px;
  background-color: darkgreen;
  color: white;
  font-size: 13px;
}
p.text {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}
</style>
